@import "../../node_modules/modern-normalize/modern-normalize.css";
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');
@import "../utils/_variables.css";

*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
    text-decoration: none;
  }
  
 * {
    padding: 0;
    margin: 0;
    color: black;
  }

img {
  display: block;
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

ul,
ol,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  margin: -1px;
  overflow: hidden;
}

.app {
  font-family: 'Roboto', sans-serif;
  min-height: 100vh;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(../images/svg/rectangleBg.svg);
  width: 100%;
}

.container {
  max-width: 100%;
  margin: auto;
  overflow: hidden;
}
@media (min-width: 320px) {
  .container {
    max-width: 320px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}
@media (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

